import React from "react";
import {Link } from 'react-router-dom';
const HeaderMenu: React.FC = () => {
  const showSideBar = () => {
    const obj = document.querySelector('.sideBar') as HTMLElement;
    obj.classList.add('sideBarView');
  }
  return (
    <>
      <div className="headerMenuCont">
        <div className="d-flex" style={{width:"100%"}}>
          <div className="showMenu">
            <svg width="21" height="21" onClick={showSideBar} viewBox="0 0 24 24">
              <path d="M24 6h-24v-4h24v4zm0 4h-24v4h24v-4zm0 8h-24v4h24v-4z"/>
            </svg>
            <img src="/assets/images/logo2.png" />
          </div>
          <div className="menuSection">
            <div className="menuItem">
              <Link to="/admin/dashboard/home">Home</Link>
            </div>
            <div className="menuItem">
              <Link to="/admin/dashboard/report">Report</Link>
            </div>
            <div className="menuItem">
              <Link to="/admin/dashboard/services">Services</Link>
            </div>
            <div className="menuItem">
              <Link to="/admin/dashboard/portfolio">Portfolio</Link>
            </div>
            <div className="companyLogo">
              <img src="/assets/images/logo2.png"/>
            </div>
          </div>
          <div className="userDetail">
            <div className="userName">Maksood aalam</div>  
            <div className="userImage">
              <img src="/assets/images/person_1.jpg" />
            </div>  
            <div className="userLogout" data-toggle="tooltip" data-placement="top" title="Logout" >
              <svg width="24" height="24" viewBox="0 0 24 24">
                <path d="M10 2v2h12v16h-12v2h14v-20h-14zm0 7.408l2.963 2.592-2.963 2.592v-1.592h-8v-2h8v-1.592zm-2-4.408v4h-8v6h8v4l8-7-8-7z"/>
              </svg>
            </div>  
          </div>
        </div>
      </div>
    </>
  );
};

export default HeaderMenu;
