import React, { Fragment } from "react";
import { Switch, Route } from "react-router";
import Home from "./Home/Home";
import Header from "./Header/Header";
import HeaderMenu from "./Header/HeaderMenu";
import Report from "./Report/Report";
import Services from "./Services/Services";
import Portfolio from "./Portfolio/Portfolio";
import '../Sass/AdminHome.scss';
const Admin: React.FC = () => {
  return (
    <Fragment>
      <Header />
      <div className="mainCont">
        <HeaderMenu />
        <div className="workSection">
          <Switch>
            <Route exact path="/admin/dashboard/home" render={ () => <Home /> } />
            <Route exact path="/admin/dashboard/report" render={ () => <Report /> } />
            <Route exact path="/admin/dashboard/services" render={ () => <Services /> } />
            <Route exact path="/admin/dashboard/portfolio" render={ () => <Portfolio /> } />
          </Switch>
        </div>
      </div>

    </Fragment>
  );
};

export default Admin;
